import { useEffect, useState } from "react";

export default function TemplateSelectedWidget({ templateName: initialName }) {
  const [templateName, setTemplateName] = useState(initialName || "Selected Template");

  useEffect(() => {
    try {
      const params = new URLSearchParams(window.location.search);
      const fromQuery = params.get("templateName");
      if (fromQuery) setTemplateName(fromQuery);
    } catch {}
  }, []);

  return (
    <div className="ts-wrap">
      <div className="ts-card">
        <div className="ts-media">Template Preview</div>
        <div className="ts-content">
          <h2 className="ts-title">{templateName}</h2>
          <p className="ts-meta">Polished layout, ATS-friendly structure, and customizable sections.</p>
          <div className="ts-actions">
            <button className="ts-btn ts-primary" onClick={() => alert("Template applied")}>Use This Template</button>
            <button className="ts-btn ts-secondary" onClick={() => { window.location.href = "/payment"; }}>Go to Payment</button>
          </div>
        </div>
      </div>
      <style>{`
        :root { --accent: #0ea5e9; --bg: #f6f7fb; --text: #0f172a; }
        .ts-wrap { max-width: 980px; margin: 24px auto; padding: 0 16px; font-family: system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif; color: var(--text); }
        .ts-card { background: #fff; border: 1px solid #e5e7eb; border-radius: 14px; overflow: hidden; box-shadow: 0 1px 2px rgba(0,0,0,.06); }
        .ts-media { aspect-ratio: 16/9; background: #f1f5f9; display: grid; place-items: center; color: #64748b; font-size: 13px; }
        .ts-content { padding: 16px; }
        .ts-title { margin: 0 0 6px; font-weight: 600; font-size: 18px; }
        .ts-meta { margin: 0; color: #64748b; font-size: 12px; }
        .ts-actions { display: flex; gap: 10px; margin-top: 14px; }
        .ts-btn { appearance: none; border: none; border-radius: 8px; padding: 10px 14px; font-weight: 600; cursor: pointer; }
        .ts-primary { background: var(--accent); color: #fff; }
        .ts-secondary { background: #0f172a; color: #fff; }
      `}</style>
    </div>
  );
}
